import {apiUrl} from '@/common/js/api.js'
import axios from 'axios'
import qs from 'qs'

// 
export const updatePhone = params => {
  return axios({
    method: 'post',
    dataType: 'json',
    url: apiUrl.updatePhone,
    headers: {Authorization:'bearer ' + localStorage.getItem('token')},
    data: params
  });
};
