<template>
    <div class="headBox">
        <el-menu
                :default-active="activeIndex"
                class="el-menu-demo"
                mode="horizontal"
                @select="handleSelect"
                background-color="#545c64"
                text-color="#fff"
                :router=true
                active-text-color="#ffd04b">
            <!--<el-menu-item index="1"></el-menu-item>-->
            <el-submenu index="1"  v-if="this.permissionHead.guanliyuan || this.permissionHead.quanxian || this.permissionHead.juese ">
                <template slot="title"></template>
                <el-menu-item index="Users" v-if="permissionHead.guanliyuan"><router-link  to="Users"></router-link></el-menu-item>
                <el-menu-item index="UsersPermission" v-if="permissionHead.quanxian"><router-link  to="UsersPermission"></router-link></el-menu-item>
                <el-menu-item index="UsersRole" v-if="permissionHead.juese"><router-link  to="UsersRole"></router-link></el-menu-item>
            </el-submenu>
            <el-submenu index="2" collapse-transition v-if="this.caseHead.caseManagement">
                <template slot="title"></template>
                <el-menu-item index="CaseUpload"><router-link to="CaseUpload"></router-link></el-menu-item>
                <el-menu-item index="FileList"><router-link to="FileList"></router-link></el-menu-item>
                <el-menu-item index="SearchCase"><router-link to="SearchCase"></router-link></el-menu-item>
                 <el-menu-item index="CaseCourt" class="hidden"><router-link to="CaseCourt"></router-link></el-menu-item>
            </el-submenu>
            <el-submenu index="3" collapse-transition v-if="this.wenjuan.questionnaireAdmin || this.wenjuan.questionnaire">
                <template slot="title"></template>
                <el-submenu index="3-1" v-if="this.wenjuan.questionnaireAdmin">
                  <template slot="title"></template>
                  <el-menu-item index="QuestionManagement"><router-link to="QuestionManagement"></router-link></el-menu-item>
                  <el-menu-item v-if="false" index="GuanLianManagement"><router-link to="GuanLianManagement"></router-link></el-menu-item>
                  <el-menu-item v-if="false" index="DisplayRule"><router-link to="DisplayRule"></router-link></el-menu-item>
                </el-submenu>
                <el-submenu index="3-2">
                  <template slot="title"></template>
                  <el-menu-item index="wenjuan" v-if="this.wenjuan.questionnaireAdmin"><router-link to="wenjuan"></router-link></el-menu-item>
                  <el-menu-item index="UserSystem" v-if="this.wenjuan.questionnaire"><router-link to="UserSystem"></router-link></el-menu-item>

                  <!-- <el-menu-item index="BasicInformation"><router-link to="BasicInformation"></router-link></el-menu-item> -->
                </el-submenu>
            </el-submenu>
        </el-menu>
        <div class="text-right text-white pr-10">
            <span class="mr-3 cursor-pointer" @click="Exit">[]</span>
            <span >{{user}}</span>
        </div>
    </div>

</template>

<script>
  import {usersSelect} from '@/api/api/requestLogin.js'  // 

  export default {
    name: 'HeadMenu.vue',
    data() {
      return {
        activeIndex: '1',
        permissions: [], // 
        permissionsId: [], // id
        roles: [],
        rolesId: [],
        permissionHead: {    // 
            guanliyuan: false,  // 
            quanxian: false,   // 
            juese: false,      // 
        },
        caseHead: {  // 
            caseManagement: false  // 
        },
        wenjuan: {   // 
            questionnaire: false,   // 
            questionnaireAdmin: false  // 
        },
        user: localStorage.getItem('name')
      };
    },
    mounted(){
      this.getUserSelect()   // 
    },
    methods: {
      handleSelect(key, keyPath) {
        // console.log(key, keyPath);
      },
      getUserSelect () { // 
        usersSelect().then((data)=>{
            this.permissions = data.data.permissions
            this.roles = data.data.roles;
            // this.roles.forEach((item)=>{
            //   this.rolesId.push(item.name)
            // })
            // this.objOfValueToArr(this.rolesId)
            if(this.permissions !== []){
                this.permissions.forEach((item)=>{
                  this.permissionsId.push(item.id)
                    if(item.id==11){
                        this.permissionHead.juese = true
                    }else if(item.id == 12){
                        this.permissionHead.quanxian = true
                    }else if(item.id == 13){
                        this.permissionHead.guanliyuan = true
                    }else if(item.id == 67){
                        this.caseHead.caseManagement = true
                    }else if(item.id == 68){
                        this.wenjuan.questionnaireAdmin = true
                    }else if(item.id == 69){
                        this.wenjuan.questionnaire = true
                    }
                })
            }
          // console.log(data.data.permissions)
        }).catch((data)=>{

        })
      },
      // objOfValueToArr(object) {
      //     var arr = [];
      //     var i = 0;
      //     for (var item in object) {
      //         arr[i] = object[item];
      //         i++;
      //     }
      //     return arr;
      // },
      Exit(){
        this.$router.replace("/");
        localStorage.removeItem('token')
        localStorage.removeItem('roleType')
      }
    }
  }
</script>

<style scoped>
.el-menu.el-menu--horizontal{
  border:none !important;
}
.headBox{
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: rgb(84, 92, 100);
}
</style>



